html, body {
  width   : 100%;
  height  : 100%;
  margin  : 0;
  padding : 0;
  font-family: 'Bloody Fonts';
  color: white;
  //overflow: hidden;
}

body {
  background: #151515;
}

.canvas-full-wrapper {
  display: block;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  outline: none;
  position: fixed;
  z-index: 2;
  pointer-events: none;
}
$width: 600px;
$height: 600px;
.canvas-wrapper {
  display: flex;
  //align-items: center;
  justify-content: center;
  padding-top: 20px;
  width   : $width;
  height  : $height;
  margin: 0 auto;
  outline: none;
}
#renderCanvas {
  display: block;
  touch-action: none;
  width   : 100%;
  height  : 100%;
  //box-shadow: 0 0 10px rgb(255 0 0);
  box-shadow: rgb(255, 0, 0) 0px 0px 53px -24px;
  outline: none;
  /*cursor:none;*/
}

.canvas-img {
  width: $width;
  height: $height;
  position: absolute;
  z-index: 1;
  pointer-events: auto;
}

#main {
  z-index: 0;
}
#renderCanvas {
  z-index: 1;
}
#moon-full {
  z-index: 2;
}
.bg-video-container {
  position: absolute;
  z-index: 1;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  pointer-events: none;
  opacity: 0.15;
}

.bg-video {
  height: 100%;
  width: 100%;
  object-fit: fill;
}
.hide {
  display: none !important;
}

.moon-full {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  pointer-events: none;
}
.bg-video-overlay {
  background: #000000cf;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.story-box {
  color: white;
  font-size: 24px;
  width: 300px;
  //border: 1px solid white;
  text-align: center;
  font-family: fantasy;
}

.story-box-container {
  position: absolute;
  width: 100%;
  height: 30px;
  top: 60px;
  display: flex;
  justify-content: center;
  z-index: 3;
}

.password-box-container {
  position: absolute;
  width: 100%;
  height: 30px;
  top: 35px;
  //bottom 15px;
  display: flex;
  justify-content: center;
  z-index: 3;

  input {
    width: 150px;
    font-size: 20px;
    font-family: fantasy;
    color: #d40707;
    box-shadow: rgb(255 255 255) 0px 0px 18px 2px;
    border-color: white;
  }
}

.page-container {
  //width: 100vw;
  //height: 100vh;
}
.left-side {
  font-family: fantasy;
  position: absolute;
  left:30px;
  top:90px;
  width: 200px;
}
